.container {
  margin: auto;
  width: 100%;
  max-width: 375px;
  padding: 25px;
}

.container div {
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-bottom: 30px;
  width: 200px;
  height: 200px;
  padding: 15px;
  border: 1px solid black;
  border-radius: 15px;
}

.container div img {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}

.backBtn {
  padding: 10px;
  color: white;
  font-size: 1rem;
  background: #ff5554;
  border: none;
  border-radius: 10px;
}
