* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.container {
  margin: auto;
  width: 100%;
  padding: 25px;
}

.container div {
  width: 100%;
  max-width: 375px;
  height: 15vh;
  border: 1px solid black;
  border-radius: 15px;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding: 10px;
}

.container div img {
  width: 100px;
  height: 100px;
  max-width: 30%;
  margin-right: 30px;
}

.container div h2 {
  font-size: 2rem;
}
