* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.container {
  margin: auto;
  width: 100%;
  max-width: 375px;
  padding: 25px;
}

.container .gridContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  row-gap: 10px;
}

.container .gridContainer .itemContainer {
  width: 100%;
  max-width: 150px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container .gridContainer .itemContainer:active {
  width: 100%;
  max-width: 150px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: yellow;
}

.container .gridContainer .itemContainer img {
  width: 130px;
}

.container .gridContainer .itemContainer h2 {
  font-size: 1rem;
}

.tabBtn {
  padding: 10px 20px;
  margin: 10px 10px;
}

.backBtn {
  padding: 10px;
  color: white;
  font-size: 1rem;
  background: #ff5554;
  border: none;
  border-radius: 10px;
  margin: 10px;
}

.bBtn {
  padding: 10px;
  color: white;
  font-size: 1rem;
  background: #546eff;
  border: none;
  border-radius: 10px;
  margin: 10px;
}
